body{
  background-color: #F5D56B;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.appContainer {
  width: 500px;
  background-color: #F48B29;
  display: flex;
  flex-wrap: wrap;
  border-radius: 1rem;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 1rem;
}
.myContainer {
  width: 200px;
  height: 140px;
  background-color: rgb(240, 234, 234);
  text-align: center;
  border-radius: 0.5rem;
  margin: 1rem auto;
}
.myContainer:hover {
  box-shadow: 2px 2px 2px 2px rgba(92, 92, 194, 0.3);
}





